"use client";

import { useEffect, useRef, useState } from "react";
import styles from "./Profile.module.css";
import toast from "react-hot-toast";
import { FaEye, FaEyeSlash } from "react-icons/fa";

import {
  getOrganizationProfileApi,
  updateOrganizationProfileApi,
  forgotApi,
  verifyOtpApi,
  resetPasswordApi,
} from "@/lib/apiClient";
import { getUserData } from "@/lib/auth";

export default function ProfilePage() {
  const [mode, setMode] = useState("view"); // view | edit | otp | password | success
  const [profile, setProfile] = useState(null);
  const [loading, setLoading] = useState(true);

  const [form, setForm] = useState({
    organizationName: "",
    domainEmail: "",
    image: null,
  });

  const [otp, setOtp] = useState("");
  const [password, setPassword] = useState("");
  const [confirm, setConfirm] = useState("");
  const [show1, setShow1] = useState(false);
  const [show2, setShow2] = useState(false);

  const fileRef = useRef(null);

  /* ================= FETCH PROFILE ================= */
  useEffect(() => {
    async function loadProfile() {
      try {
        const user = getUserData();
        if (!user?.identity) return;

        const res = await getOrganizationProfileApi(user.identity);
        if (res?.status) {
          setProfile(res.data);
          setForm({
            organizationName: res.data.organizationName || "",
            domainEmail: res.data.domainEmail || "",
            image: null,
          });
        }
      } catch {
        toast.error("Failed to load profile");
      } finally {
        setLoading(false);
      }
    }

    loadProfile();
  }, []);

  /* ================= SAVE PROFILE ================= */
  const saveProfile = async () => {
    try {
      const user = getUserData();
      const payload = new FormData();
      payload.append("organizationName", form.organizationName);
      payload.append("domainEmail", form.domainEmail);
      if (form.image) payload.append("image", form.image);

      const res = await updateOrganizationProfileApi(user.identity, payload);
      if (res?.status) {
        setProfile(res.data);
        toast.success("Profile updated");
        setMode("view");
      }
    } catch {
      toast.error("Update failed");
    }
  };

  /* ================= PASSWORD FLOW ================= */
  const sendOtp = async () => {
    await forgotApi({ email: form.domainEmail });
    toast.success("OTP sent");
    setMode("otp");
  };

  const verifyOtp = async () => {
    await verifyOtpApi({ email: form.domainEmail, otp });
    toast.success("OTP verified");
    setMode("password");
  };

  const resetPass = async () => {
    if (password !== confirm)
      return toast.error("Passwords not matching");

    await resetPasswordApi({
      email: form.domainEmail,
      password,
    });

    toast.success("Password updated");
    setMode("success");
  };

  if (loading) return <p>Loading...</p>;
  if (!profile) return <p>No profile</p>;

  return (
    <div className={styles.wrapper}>
      {/* ================= VIEW ================= */}
      {mode === "view" && (
        <div className={styles.viewBox}>
          <div>
            <label>Full Name</label>
            <p>{profile.organizationName}</p>
          </div>

          <div>
            <label>Domain Email</label>
            <p>{profile.domainEmail}</p>
          </div>

          <img
            src="/images/Pen.png"
            className={styles.editIcon}
            onClick={() => setMode("edit")}
          />
        </div>
      )}

      {/* ================= EDIT ================= */}
      {mode === "edit" && (
        <div className={styles.editBox}>
          <div className={styles.left}>
            <label>Full Name</label>
            <input
              value={form.organizationName}
              onChange={(e) =>
                setForm({ ...form, organizationName: e.target.value })
              }
            />

            <label>Domain Email</label>
            <input
              value={form.domainEmail}
              onChange={(e) =>
                setForm({ ...form, domainEmail: e.target.value })
              }
            />

            <span className={styles.changePass} onClick={sendOtp}>
              Change Password
            </span>
          </div>

          <div>
            <div
              className={styles.uploadBox}
              onClick={() => fileRef.current.click()}
            >
              <p>
                {form.image
                  ? form.image.name
                  : "Upload profile picture"}
              </p>
            </div>

            <input
              ref={fileRef}
              type="file"
              hidden
              accept="image/*"
              onChange={(e) =>
                setForm({ ...form, image: e.target.files[0] })
              }
            />

            <div className={styles.btnRow}>
              <button onClick={() => setMode("view")}>Cancel</button>
              <button onClick={saveProfile}>Save</button>
            </div>
          </div>
        </div>
      )}

      {/* ================= OTP ================= */}
      {mode === "otp" && (
        <div className={styles.centerBox}>
          <h3>Enter OTP</h3>
          <input
            maxLength={4}
            value={otp}
            onChange={(e) => setOtp(e.target.value)}
          />
          <div className={styles.btnRow}>
            <button onClick={() => setMode("edit")}>Cancel</button>
            <button onClick={verifyOtp}>Continue</button>
          </div>
        </div>
      )}

      {/* ================= PASSWORD ================= */}
      {mode === "password" && (
        <div className={styles.centerBox}>
          <div className={styles.passWrap}>
            <input
              type={show1 ? "text" : "password"}
              placeholder="New password"
              onChange={(e) => setPassword(e.target.value)}
            />
            <span onClick={() => setShow1(!show1)}>
              {show1 ? <FaEyeSlash /> : <FaEye />}
            </span>
          </div>

          <div className={styles.passWrap}>
            <input
              type={show2 ? "text" : "password"}
              placeholder="Confirm password"
              onChange={(e) => setConfirm(e.target.value)}
            />
            <span onClick={() => setShow2(!show2)}>
              {show2 ? <FaEyeSlash /> : <FaEye />}
            </span>
          </div>

          <div className={styles.btnRow}>
            <button onClick={() => setMode("edit")}>Cancel</button>
            <button onClick={resetPass}>Save</button>
          </div>
        </div>
      )}

      {/* ================= SUCCESS ================= */}
      {mode === "success" && (
        <div className={styles.success}>
          Password Successfully Changed!!
        </div>
      )}
    </div>
  );
}
